/* https://flatuicolors.com/palette/us */

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,700;0,900;1,300&display=swap');


:root{
   --colorPrimary:#6c5ce7;
   --colorParagraph:#2d3436;
}

*{
   /* font-family:  Helvetica, Arial, sans-serif; */
   font-family: 'Poppins', sans-serif;
}

body{
   padding: 5px;
   background-color: #fdfdfd;
}

input{
   border-radius: 5px;
   border: 1px solid rgba(0, 0, 0, 0.39);
   color:gray;
   font-size: 1rem;
   height: 25px;
   padding:5px 0 5px 5px;
   width: 100%;
}

p, h3, li{
   color:var(--colorParagraph);
}

ol {
   padding:5px;
}

.logo{
   color:var(--colorPrimary);
   font-weight: 600;
   font-size: calc(30px + 2vw);
   margin:10px;
   padding: 20px 0;
   text-align: center;
   width: auto;
}


.card-grid{
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
}

.card{
   align-items: center;
   border-radius: 8px;
   border:1px solid var(--colorParagraph);
   margin-bottom: 10px;
   margin-right: 10px;
   overflow: hidden;
   text-align: center;
   height: min-content;
}

.card img{
  max-height: 200px;
}

.card p {
   color:var(--colorParagraph);
   font-family: 'Poppins', sans-serif;
   font-style: italic;
   font-weight: 500;
}